body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  height: 100vh; }
  .App .venueList {
    top: 36px; }
  .App .searchInput {
    height: 30px;
    width: 100%;
    background-color: white;
    padding: 10px;
    font-size: 14pt;
    font-family: Arial, Helvetica, sans-serif;
    color: black; }
  .App ul.places-list {
    margin: 0px;
    height: 100%;
    padding-left: 0px;
    background-color: #444; }
  .App ul.places-list li {
    list-style: none;
    margin-bottom: 0.2em;
    line-height: 1.2;
    text-align: left;
    color: #fffce1;
    border-bottom: 1px solid lightgray;
    padding-left: 0.5em; }
  .App ul.places-list li:focus,
  .App .places-list li:hover {
    outline: 0;
    color: #e64f4f;
    cursor: pointer; }
  .App ul.places-list li a {
    display: block;
    width: 100%;
    height: 100%;
    padding-left: 0.5em;
    text-decoration: none;
    color: inherit; }
  .App .venueAddress {
    font-size: 11px;
    margin-top: 1.5em;
    margin-bottom: 0.5em; }
  .App ::-webkit-scrollbar {
    width: 0px;
    /* remove scrollbar space */
    background: transparent;
    /* optional: just make scrollbar invisible */ }

